import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";

import Home from "./components/Home";
import YaraBela from "./components/YaraBela";
import YaraMila from "./components/YaraMila";
import YaraLiva from './components/YaraLiva';
import YaraVera from './components/YaraVera';
import YaraVita from './components/YaraVita';

const App = () => (
    <Router>
      <div>
        <Route path="/pwa-test-project/" exact component={Home} />
        <Route path="/pwa-test-project/bela" component={YaraBela} />
        <Route path="/pwa-test-project/mila" component={YaraMila} />
        <Route path="/pwa-test-project/liva" component={YaraLiva} />
        <Route path="/pwa-test-project/vera" component={YaraVera} />
        <Route path="/pwa-test-project/vita" component={YaraVita} />
      </div>
    </Router>
)
export default App;



