import React from "react";
import { Link } from "react-router-dom";

import './FertilizerPage.css'

const YaraMila= () => {
  return (
    <div className="FertilizerPage">
      <div className="FertilizerImageContainer">
        <div className="InfoText">
          <p className="FertilizerTitle">
          Homogeneous NPKs - YaraMila
          </p>
          <div className="TextAlignment">
            <p>
            The YaraMila® range of NPK fertilizers has a balance of nitrogen, phosphorus and potassium that suits specific nutritional needs of your crop throughout its growing season. In addition, the range includes formulations with secondary nutrients (magnesium and sulfur) and micronutrients (boron, iron, manganese, molybdenum, and zinc).
            </p>
            <p>
            These homogeneous NPK fertilizers will help you maximize crop yield and quality. Each YaraMila compound, whether in prill or granule form, is designed for high value crops such as vegetables, fruit trees, vines and turf.
            </p>

          </div>
        </div>
        <div>
        <img className="InfoImage" src="https://www.yara.us/siteassets/crop-nutrition/fertiliser-choices/yaramila-logo-tagline-2017-12-08.png"></img>
        </div>
      </div>
      <div className="FertilizerText">
        <div className="InfoText--NoImage">
        <p>
            YaraMila NPK fertilizers benefit the grower because they provide even distribution of nutrients, whether spread by hand or machine. There is no risk of nutrient segregation during shipping, handling or spreading.
            </p>
        </div>
      </div>
      <p className="Button">
        <Link to="/pwa-test-project/">Back</Link>
      </p>
    </div>
  );
};

export default YaraMila;



