import React from "react";
import { Link } from "react-router-dom";

import './FertilizerPage.css'

const YaraLiva = () => {
  return (
    <div className="FertilizerPage">
      <div className="FertilizerImageContainer">
        <div className="InfoText">
          <p className="FertilizerTitle">
          Homogeneous NPKs - YaraMila
          </p>
          <div className="TextAlignment">
            <p>
            Yara's comprehensive range of calcium nitrate fertilizers that help ensure high-value, highly marketable produce, are called YaraLiva.
            </p>
            <p>
            YaraLiva calcium nitrates provide fast-acting nitrate-N, alongside strength-building calcium. In combination, these nutrients fuel prolonged growth. Plants and trees treated with YaraLiva-branded fertilizers are naturally healthier and less sensitive to stress during growth. Then, critically, in the build-up to harvest, YaraLiva calcium nitrate fertilizers improve the size, strength and appearance of the fruit, tuber, leaf or lettuce.
            </p>

          </div>
        </div>
        <div>
        <img className="InfoImage" src="https://www.yara.us/siteassets/crop-nutrition/fertiliser-choices/yaraliva-logo-tagline-red.png"></img>
        </div>
      </div>
      <div className="FertilizerText">
        <div className="InfoText--NoImage">
          <p>
          The YaraLiva formulation includes nitrates, the preferred N form. Crops respond more quickly to nitrate. It is also more mobile in the soil and immediately available to the crop.
          </p>
          <p>
          By using YaraLiva fertilizers you will get a longer lasting fruit and vegetable with improved inner health and outer beauty.
          </p>
        </div>
      </div>
      <p className="Button">
        <Link to="/pwa-test-project/">Back</Link>
      </p>
    </div>
  );
};

export default YaraLiva;



